import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  public key = '5b9a859a379226d16d7405e03c7950fc';
  public baseUrl = 'http://api.openweathermap.org/';
  public cityUrl = 'data/2.5/weather?q=';

  constructor(private http: HttpClient) { }

  getCountryCodeByLatAndLon(lat: number, lon: number): Observable<any> {
    return this.http.get(this.baseUrl + 'geo/1.0/reverse?lat=' + lat + '&lon=' + lon + '&limit=1&appid=' + this.key + '');
  }

  getWeatherByCityName(query: string): Observable<any> {
    return this.http.get(this.baseUrl + this.cityUrl + '' + query + '&appid=' + this.key + '');
  }


}
